<template>
  <b-form>
    <b-card title="Add namespace" style="max-width: 28rem;" class="mb-2">
      <b-card-text>
        <b-form-group>
          <b-form-input v-model="form.name" placeholder="name"></b-form-input>
          <b-form-invalid-feedback :state="isValidName">The name must be at least 4 characters long</b-form-invalid-feedback>
          <b-form-valid-feedback :state="isValidName">
            <b-icon-check class="text-success" />Ok
          </b-form-valid-feedback>
        </b-form-group>
      </b-card-text>
      <b-button variant="success" v-if="isValidName" @click="postForm">Save</b-button>
      <b-button variant="success" v-else disabled>Save</b-button>
      <b-button variant="warning" class="ml-2" @click="$store.commit('endAction')">Cancel</b-button>
    </b-card>
  </b-form>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: ""
      }
    };
  },
  methods: {
    postForm() {
      let vue = this;
      vue.$axios
        .post("/namespaces/add", {
          name: this.form.name
        })
        .then(function() {
          vue.$bvToast.toast("ok", {
            title: "Namespace saved",
            variant: "success",
            autoHideDelay: 1500
          });
          vue.form.name = "";
          vue.$emit("refresh");
          vue.$store.commit("endAction");
        })
        .catch(e => {
          if (e.response.status !== 200) {
            if (e.response.status === 409) {
              vue.$bvToast.toast(
                `${e.response.status} ${e.response.data.error}`,
                {
                  title: "Error",
                  variant: "danger"
                }
              );
              this.form.name = "";
            }
          } else {
            console.log(e);
          }
        });
    }
  },
  computed: {
    isValidName() {
      return this.form.name.length >= 4;
    }
  }
};
</script>