<template>
  <div>
    <h1 class="text-muted mt-3">Namespaces</h1>
    <div>
      <b-collapse id="collapse-4" v-model="showActionBar" class="mt-2">
        <add v-if="action === 'addNamespace'" @refresh="fetchNamespaces"></add>
      </b-collapse>
    </div>
    <b-table striped hover :items="data" :fields="fields" class="mt-4" style="max-width:450px">
      <template v-slot:cell(action)="data">
        <b-button
          variant="danger"
          @click="confirmDeleteNamespace(data.item.id, data.item.name)"
        >Delete</b-button>
      </template>
    </b-table>
    <b-modal title="Delete namespace" ref="delete-modal">
      Delete {{ namespaceToDelete }}?
      <template v-slot:modal-footer="{ ok, cancel }">
        <b-button variant="danger" @click="deleteNamespace(data.item.id)">Delete</b-button>
        <b-button variant="warning" @click="cancel()">Cancel</b-button>
      </template>
    </b-modal>
  </div>
</template>


<script>
import { mapState, mapGetters } from "vuex";
import Add from "@/components/namespace/Add";

export default {
  components: {
    Add
  },
  data() {
    return {
      data: [],
      fields: ["id", "name", "action"],
      namespaceToDelete: null
    };
  },
  methods: {
    fetchNamespaces() {
      this.$axios
        .get("/namespaces/all")
        .then(response => (this.data = response.data))
        .catch(error => console.log(error));
    },
    confirmDeleteNamespace(id, name) {
      this.namespaceToDelete = name;
      this.$refs["delete-modal"].show();
    },
    deleteNamespace(id) {
      console.log(id);
    }
  },
  mounted: function() {
    this.fetchNamespaces();
  },
  computed: {
    ...mapState(["action"]),
    ...mapGetters({
      s: "showActionBar"
    }),
    showActionBar: {
      get() {
        return this.s;
      },
      set(newName) {
        return newName;
      }
    }
  }
  //computed: mapState(["action", "showActionBar"])
};
</script>